<template>
  <div class="ElectronicsBox">
    <p class="Ele" 
        :style="{ 
            color: Numcolor, 
            background: BGcolor , 
            fontSize: fontSize + 'px'
        }" >
        {{displayNumber}}
    </p>
  </div>
</template>
<script>
import {ref, onMounted, onUnmounted,} from "vue";
export default {
  name: 'NumAnimation',
// :EleNumber     
// :fontSize      
// :Numcolor      
// :BGcolor            none
// :Numspeed                100
// :Numfrequency              20
  props:{
    EleNumber:{
        type:Number,
        default:9999
    },
    BGcolor:{
        type:String,
        default:'none'
    },
    Numcolor:{
        type:String,
        default:'#000'
    },
    fontSize:{
        type:Number,
        default:20
    },
    Numspeed:{
        type:Number,
        default:100
    },
    Numfrequency:{    
        type:Number,
        default:20
    }
  },
  setup(props) {
        const MyEleNumber = ref(props.EleNumber)   //
        const displayNumber = ref(0)      //
        let numTime = null    //
        onMounted(()=>{
            animationNum()
        })
        // 
        onUnmounted(() => {
            window.clearInterval(numTime);
            numTime = null;
        });
        // 
        const animationNum = function (){
            // 1
            let pace = 1                  
            //Numfrequency
            if(MyEleNumber.value > props.Numfrequency){     //20     100 --> 5 
                pace = parseInt(MyEleNumber.value / props.Numfrequency)   //
            }
            numTime = setInterval(()=>{
                displayNumber.value += pace    //
                if(displayNumber.value >= MyEleNumber.value ){    //
                    clearInterval(numTime)
                    numTime = null                        //
                    displayNumber.value = MyEleNumber.value      //
                }
            },props.Numspeed)
        }
        return{
            displayNumber
        }
  }
}
</script>
<style>
/* main.js */
.Ele{
    text-align: center;
    font-family: ele;
    /*  */
    letter-spacing:7px;
    margin: 10px 0;
    /* font-size: 60px; */
     /* color */
    /* color: #00ffff; */
    /* -webkit-text-fill-color: #00ffff; */
    /*  */
    /* -webkit-text-stroke: 1px #000000;   */
}
.ElectronicsBox{
  width: 100%;
  height: 100%;
}
</style>

