<template>
  <div
    class="home"
    :class="{
      small: screenWidth < 480,
      min: screenWidth < 700,
      med: screenWidth < 1200,
    }"
  >
    <!--  -->
    <div class="header">
      <div class="title">
        <!--  -->
        <span class="name"></span>
        <p>
          <span class="describe"></span>
          <span class="webUrl" @click="copyUrL"
            >https://subtoph.github.io</span
          >
        </p>

        <!--  -->
        <div class="nav" v-if="screenWidth > 800">
          <router-link  v-for="item in navList" :to='item.naviIndex' :key="item.naviIndex">  {{ item.name }}</router-link>
        </div>
        <!--  -->
        <div class="nav" v-else>
          <span
            class="iconfont icon-24px"
            @click="showNavlist = !showNavlist"
          ></span>
        </div>
        <!--  -->
        <div class="navlist" v-if="showNavlist && screenWidth < 800">
          <router-link  v-for="item in navList" :to='item.naviIndex' :key="item.naviIndex">  {{ item.name }}</router-link>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="contentBox">
      <!--  -->
      <div class="content">
        <router-view />
      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref, reactive, onMounted } from "vue";
import { useRouter } from "vue-router";

export default defineComponent({
  name: "Home",
  components: {},
  setup() {
    const router = useRouter();
    const showNavlist = ref(false);    //
    const screenWidth = ref(null);     //
    const actNavIndex = ref(null);     //
    const navList = reactive(          //
      [
        {
          name: "",
          naviIndex: "/notes",
        },
        {
          name: "",
          naviIndex: "/assembly",
        },
        {
          name: "",
          naviIndex: "/about",
        },
      ]
    );
    onMounted(() => {
  
      monitorWidth();     //
    });
    // 
    const monitorWidth = function () {
      //
      screenWidth.value = document.body.clientWidth;
      window.addEventListener("resize", () => {
        //
        screenWidth.value = document.body.clientWidth;
      });
    };
    // 
    const copyUrL = function () {
      // input
      const input = document.createElement("input");
      // body
      document.body.appendChild(input);
      // input
      input.setAttribute("value", "https://subtoph.github.io");
      // 
      input.select();
      // 
      // input
      // Input
      if (document.execCommand("copy")) {
        document.execCommand("copy");
      }
      alert("https://subtoph.github.io"); //
      // 
      document.body.removeChild(input);
    };

    return {
      showNavlist,
      screenWidth,
      actNavIndex,
      navList,
      copyUrL,
    };
  },
});
</script>

<style lang="less" scoped>
a{
  margin: 0 10px;
  float: left;
  text-decoration:none;
  transition: 0.3s;
  color: rgb(68, 68, 68);
  line-height: 40px;
  text-align: center;
  box-sizing: border-box;
}
a:hover, a.router-link-exact-active {
  color: #0022e0;
}
.home {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgb(233, 233, 233);
  .header {
    position: fixed;
    height: 40px;
    background: #fff;
    width: 100%;
    z-index: 99;
    .title {
      width: 80%;
      line-height: 40px;
      margin: 0 auto;
      padding: 0 20px;
      box-sizing: border-box;
      overflow: hidden;
      span {
        display: block;
        float: left;
      }
      p {
        margin: 0;
        float: left;
        margin-left: 30px;
        span {
          float: none;
          line-height: 20px;
          color: #aaa;
          cursor: pointer;
        }
      }
      .name {
        font-weight: 700;
        font-size: 24px;
        font-style: italic;
      }
      .nav {
        float: right;
        font-size: 16px;
        cursor: pointer;
        .iconfont {
          -moz-user-select: none; /**/
          -webkit-user-select: none; /*webkit*/
          -ms-user-select: none; /*IE10*/
          -khtml-user-select: none; /**/
          user-select: none;
          font-size: 24px;
        }
      }
      .navlist {
        position: absolute;
        right: 0;
        top: 45px;
        width: 100px;
        background: #fff;
        border-radius: 5px;
        box-shadow: 0px 1px 4px 0px rgb(146, 146, 146);
        a{
          width: 100%;
          text-align: center;
          margin: 0;
        }
      }
    }
  }
  .contentBox {
    position: absolute;
    background: rgb(0, 38, 82);
    height: 100%;
    width: 100%;
    padding-top: 42px;
    display: flex;
    box-sizing: border-box;
    transition: 0.3s;
    .content {
      position: relative;
      margin: 0 auto;
      width: 100%;
      background: #fff;
      border-radius: 5px;
      padding: 10px 0px;
      box-sizing: border-box;
    }
  }
}
// 
.home.min .header .name {
  font-size: 16px;
}
// 
.home.med .header .title {
  width: 100%;
  padding: 0 5px;
}

.med .header .title p {
  margin: 0;
}
.min .header .title p {
  font-size: 12px;
}
// 
.min .header .title .name {
  display: none;
}
</style>
